/*signInのCSS*/
.form_container {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(219, 219, 219);
    display: flex;
    align-items: center;
  }
  
  .form_title {
    text-align: left;
    margin-bottom: 10%;
  }
  .form_wrapper {
    width: 80%;
    margin-left: 10%;
  }
  .form_outer {
    background-color:#ffffff;
    border-radius: 1%;
    padding-top: 5%;
    padding-bottom: 5%;
    padding-left: 10%;
    padding-right: 10%;
  }
  .error_message {
    margin-bottom: 5%;
  }
  
  .subsection{
    margin-bottom: 5%;
  }
  
  .text_field {
    padding-top: 1%;
  }
  .text_field p {
    margin-top: 0px; /* 隙間のサイズをピクセルで指定 */
  }
  
  .button_field {
    margin-top: 2vh;
  }
  
  .link_section {
    margin-top: 5%;
    display: flex;
    flex-direction: column;
  }

  
  .form_text{
    text-align: left;
    font-size: 1.5ex;
    color: #7F7F7F;
  }
  
  fieldset {
    border: none;
    margin: 0;
    padding: 0;
  }
  .input_section{
    margin-bottom: 10%;
  }
  
  .input_subsection{
    margin-bottom: 6%;
  }
  
  .input_subsection label {
    text-align: left;
    font-size: 1.5ex;
    font-style: bold;
  }
  .text_field{
    height: 50%;
  }
  .text_field p{
    margin-left: 2px;
  }

  .linkItem {
    margin-top: 3%;
  }
  .linkItem a{
    font-size: 1.8ex;
    text-decoration: none;
    color: #1558d6;

  }
  .linkItem a:hover {
    color: #1558d6;
    text-decoration: underline;
  }
  .linkItem ul{
    padding-left:0;
  }
  
  .linkItem li{
    list-style:none;
  }

  @media (max-width: 768px){
    .form_wrapper {
        width: 80%;
        margin-left: 10%;
    }
  }